.wp-block-create-block-simplestickynote {
	border-radius: 4px;
	box-shadow: 1px 5px 6px rgba(0, 0, 0, 0.4);
	font-family: inherit;
	word-break: break-word;
	transform-origin: top center;
	transition: transform 0.6s ease; // <-- added for smooth rotation
	will-change: transform;
	transform: rotate(-1.5deg);
	position: relative;
	color: #333;
	background: rgb(255, 249, 196);// <-- default pale color
	border: 1px solid rgba(0, 0, 0, 0.1); // <-- makes border a bit darker.
	animation: note-swing 1.2s cubic-bezier(.22,.61,.36,1) forwards;
}

@keyframes note-swing {
	0%   { transform: rotate(0deg); }
	12%  { transform: rotate(6deg); }    /* swing 1 right */
	24%  { transform: rotate(-5deg); }   /* swing 2 left  */
	36%  { transform: rotate(4deg); }    /* swing 3 right */
	52%  { transform: rotate(-3deg); }   /* swing 4 left  */
	70%  { transform: rotate(2deg); }    /* swing 5 right */
	88%  { transform: rotate(-1.5deg); } /* swing 6 left  */
	100% { transform: rotate(-1.2deg); } /* natural rest  */
}

@media (prefers-reduced-motion: reduce) {
  .wp-block-create-block-simplestickynote {
    animation: none !important;
    transform: rotate(-1.5deg) !important;
  }
}

.wp-block-create-block-simplestickynote a {
  color: inherit;
}

/* Adds the pin */
.wp-block-create-block-simplestickynote::before {
	content: "";
	position: absolute;
	border-radius: 50%;
	border: 2px solid #333;
	width: 16px;
	height: 16px;
	background: #333 radial-gradient(circle at center, rgba(255,255,255,0.6) 30%, transparent 31%);
	top: -7px;
	left: 50%;
	transform: translateX(-50%);
}

.wp-block-create-block-simplestickynote:hover {
	transform: rotate(-1.5deg);
	animation: none;
	
}



/* Default note width */
.wp-block-create-block-simplestickynote {
  max-width: 22rem;   // or whatever “narrow” means for you
  width: 100%;
  margin-left: 0;     // left aligned by default
  margin-right: auto;
}

/* If the user chooses wide or full, remove the max-width */
.wp-block-create-block-simplestickynote.alignwide,
.wp-block-create-block-simplestickynote.alignfull {
  max-width: none;
}